/* Navbar.css */

body {
  margin: 0;
  padding: 0;
}

.navbar {
  background-color: #f2f2f2; /* Light grey background for navbar */
  height: 60px;
  display: flex;
  justify-content: space-between; /* Space between the two div elements */
  align-items: center; /* Vertically center the items */
  padding: 0 20px; /* Add some padding for better spacing */
}

.nav-list {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
}

.nav-item {
  padding: 0 15px;
}

.nav-item a {
  color: black;
  text-decoration: none;
  font-weight: bold;
  font-size: 16px;
  transition: color 0.3s;
}

.nav-item a:hover {
  color: #555; /* Darken the text on hover */
}

.signin {
  background-color: black;
}

.signin a {
  color: white;
}
