/* BodyComponent.css */

body {
  background-color: #f2f2f2; /* Light grey background for the body */
  margin: 0;
  padding: 0;
}

.body-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
}

.left-content {
  margin-left: 30px;
  width: 50%;
  font-size: 24px;
  text-align: left; /* Align the content to the left */
}

.review-section {
  width: 50%;
  flex: 1;
  display: flex;
  flex-direction: column; /* Stack the elements in a column */
  align-items: center; /* Center the content horizontally */
  padding: 20px;
  /* background-color: #fff;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.1); */
}

.reviewBody {
  margin-left: 5px;
  font-size: x-large;
  width: 500px;
  height: 300px;
  padding-left: 60px;
  margin-bottom: 20px; /* Add some spacing between reviewBody and camel */
  background-color: #ffffff;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.1);
}

.camel img {
  width: 700px;
}

/* Placeholder styles for icons */
.start-icon,
.student-icon,
.instructor-icon {
  margin-right: 5px;
}
.learnmore {
  font-size: 17px;
  color: grey;
}

.learn-more-btn {
  background-color: black;
  color: #fff;
  padding: 10px;
  border-radius: 5px;
}

.reviewBody {
}

.quote {
  margin-top: 110px;
  font-weight: bold;

  color: black;
}
.quote hr {
  width: 50%;
  margin-left: -1px;
}
.quotep {
  opacity: 30%;
}

.quote hr {
  /* opacity: 0%; */
  border: 0;
  height: 4px;
  background-color: orange; /* Make the hr red */
  font-weight: bold; /* Make the hr bold */
  width: 250px; /* Set the width of the hr */
}
.quotep:hover {
  opacity: 100%;
  color: black;
}
